import { React, useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Container, Owner, Loading, BackButton, IssuesList, PageActions, FilterList } from './styles';
import { FaArrowLeft } from 'react-icons/fa';
import api from '../../services/api';

export default function Repositorio() {
  // id para usar parametros do axios
  const id = useParams();

  // useStates
  const [repositorio, setRepositorio] = useState({});
  const [issues, setIssues] = useState([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const [filters, setFilters] = useState([
    {state: 'all', label: 'Todas', active: true},
    {state: 'open', label: 'Abertas', active: false},
    {state: 'closed', label: 'Fechadas', active: false}
  ]);
  const [filterIndex, setFilterIndex] = useState(0);

  // Carregar na página o repositório
  useEffect(() => {
    async function load() {
      // Decodificando para a url funcionar
      const nomeRepo = decodeURIComponent(id.repositorio);

      // em uma só Promisse, pegar da api o repositório e as issues
      const [repositorioData, issuesData] = await Promise.all([
        api.get(`/repos/${nomeRepo}`),
        api.get(`/repos/${nomeRepo}/issues`, {
          params: {
            state: filters.find(f => f.active).state,
            per_page: 5
          }
        })
      ]);

      setRepositorio(repositorioData.data);
      setIssues(issuesData.data);
      setLoading(false);
    };

    load();

  }, [id.repositorio, filters]);

  // Carregar na página as issues do repositório
  useEffect(() => {
    async function loadIssue() {
      // Decodificando para a url funcionar com o nome do repositorio
      const nomeRepo = decodeURIComponent(id.repositorio);

      // pegando do botão de filtro quais issues será renderizado na página
      const response = await api.get(`/repos/${nomeRepo}/issues`, {
        params: {
          state: filters[filterIndex].state,
          page,
          per_page: 5
        }
      });

      setIssues(response.data);
    };

    loadIssue();
  }, [id.repositorio, page, filters, filterIndex]);

  // setar a página de issues
  function handlePage(action) {
    setPage(action === 'back' ? page - 1 : page + 1);
  };

  // setar o filtro das issues
  function handleFilter(index) {
    setFilterIndex(index);
  };

  // se a página estiver carregando o repositório, um h1 "carregando" é renderizado na página
  if(loading) {
    return (
      <Loading>
        <h1>Carregando...</h1>
      </Loading>
    );
  };

  return (
    <Container>
      <BackButton to='/'>
        <FaArrowLeft color='#555' size={ 14 }/>
      </BackButton>

      <Owner>
        <img 
          src={ repositorio.owner.avatar_url }
          alt={ repositorio.owner.login }
        />
        <h1>{ repositorio.name }</h1>
        <p>{ repositorio.description }</p>
      </Owner>

      

      <FilterList active={filterIndex}>
        {filters.map((filter, index) => {
          return (

            <button 
              type='button'
              key={ filter.label }
              onClick={() => handleFilter(index)}
            >
              { filter.label }
            </button>

          );
        })}
      </FilterList>

      <IssuesList>
        {issues.map(issue => {
          return (
            <li key={String(issue.id)}>
              <img src={ issue.user.avatar_url } alt={ issue.user.login } />

              <div>
                <strong>
                  <a href={ issue.html_url }>{ issue.title }</a>

                  {issue.labels.map(label => {
                    return (
                      <span key={ String(label.id) }>{ label.name }</span>
                    );
                  })};
                </strong>

                <p>{ issue.user.login }</p>
              </div>
            </li>
          );
        })}
      </IssuesList>

      <PageActions>
        <button
          type='button'
          onClick={() => handlePage('back')}
          disabled={page < 2}
        >
          Voltar
        </button>

        <button
          type='button'
          onClick={() => handlePage('next')}
        >
          Próximo
        </button>
      </PageActions>
    </Container>
  );
};