import styled, { keyframes, css } from 'styled-components';

export const Container = styled.div`
    max-width: 700px;
    background: #f3f3f3;
    border-radius: 5px;
    padding: 30px;
    margin: 80px auto;
    box-shadow: 0 0 20px rgba(0,0,0,.2);

    h1 {
        font-size: 20px;
        display: flex;
        align-items: center;

        svg {
            margin-right: 10px;
        }
    }
`;

export const Form = styled.form`
    margin-top: 30px;
    display: flex;

    input {
        flex: 1;
        border: 1px solid ${ props => (props.error ? '#ff0000' : '#eee')};
        padding: 10px 15px;
        border-radius: 5px;
        font-size: 14px;
    }
`;

// Animação do FASpinner
const animate = keyframes`
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
`;

export const SubmitButton = styled.button.attrs(props => ({
    type: 'submit',
    disabled: props.loading,
}))`
    background: #0d2636;
    border: none;
    border-radius: 5px;
    margin-left: 10px;
    padding: 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;

    &[disabled] {
        cursor: not-allowed;
        opacity: .5;
    }

    ${ props => props.loading &&
        css`
            svg {
                animation: ${ animate } 2s linear infinite;
            }
        `
    }
`;

export const List = styled.ul`
    list-style: none;
    margin-top: 20px;

    li {
        padding: 15px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;

        //Primeiro li será ignorado
        & + li {
            border-top: 1px solid #eee;
        }

        a {
            color: #0D2636;
            text-decoration: none;
        }
    }
`;

export const DeleteButton = styled.button.attrs({
    type: 'button'
})`
    margin-left: 6px;
    background: transparent;
    color: #0D2636;
    border: none;
    padding: 8px;
    outline: 0;
    border-radius: 5px;
`;